.control {
  align-self: stretch;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  padding: 0 20px;
  border-left: 1px solid $color-night-rider;
  box-sizing: border-box;
  transition: background-color 0.6s ease;

  &:hover {
    background-color: $color-zambezi;
  }

  &:active {
    background-color: $color-night-rider;
  }

  @include vp-1919 {
    padding: 0 15px;
  }

  &--login {
    max-width: 122px;
  }

  &--mobile {
    @include vp-1279 {
      margin-bottom: 15px;
      padding: 0;
      border: none;
      outline: 1px dashed orange;
    }
  }
}

//   

.control__icon {
  padding-right: 10px;
  line-height: 0;
}

.control--region .control__icon,
.control--login .control__icon {
  @include vp-1919 {
    padding-right: 0;
  }
}

.control__icon svg {
  color: $color-alizarin-red;
  width: 16px;
  height: 24px;
}

//  

.control--login .control__content {
  @include vp-1919 {
    display: none;
  }
}
