.phone__number {
  margin: 0;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  color: $color-default-white;

  @include vp-1919 {
    font-size: 14px;
  }
}

.phone--mobile .phone__number {
  color: $color-default-black;
  font-size: 14px;
}

.phone__description {
  margin: 0;
  font-size: 12px;
  line-height: 14px;
  font-weight: 400;
  color: $color-aluminum;

  @include vp-1919 {
    font-size: 10px;
  }
}
