.region-select {

  &--header {
    @include vp-1919 {
      display: none;
    }
  }

  &--mobile {
    margin-left: 10px;
  }
}

.region-select__text {
  display: block;
  margin: 0;
  font-size: 12px;
  line-height: 14px;
  color: $color-aluminum;
}

.region-select--mobile .region-select__text {
  font-size: 10px;
  font-weight: 400;
}


.region-select__city-name {
  position: relative;
  display: block;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  color: $color-default-white;
  text-decoration: underline;
  text-decoration-style: dashed;
  text-decoration-color: $color-alizarin-red;

  &::after {
    content: "";
    position: absolute;
    top: 5px;
    right: 10px;
    display: block;
    width: 12px;
    height: 7px;
    background: url("../img/svg/caret.svg") no-repeat right;
  }
}

.region-select--mobile .region-select__city-name {
  color: $color-default-black;

  &::after {
    right: 0;
  }
}
